.footer {
  padding-top: 40px;
  padding-bottom: 15px;
  background-image: url('../images/footer-bg_mobile.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  @media screen and (min-width: 768px) {
    background-image: url('../images/footer-bg.png');
  }
}
.footer-content {
  display: flex;
  flex-direction: column;
  row-gap: 40px;
  margin-bottom: 40px;
  @media screen and (min-width: 768px) {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 100px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 132px;
  }
}
.logo {
  display: flex;
  // margin-bottom: 40px;
  font-family: $font-family;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.18;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: $white;
  .logo-icon {
    width: 18px;
    height: 18px;
  }
  .menu-link {
    font-family: $font-family;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.18;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: $white;
  }
}
.navigation-footer {
  .menu {
    display: flex;
    flex-direction: column;
    row-gap: 14px;
    @media screen and (min-width: 1200px) {
      flex-direction: row;
      column-gap: 25px;
    }
    .menu-item {
      .menu-link {
        font-family: $font-family;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.18;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: $white;
      }
    }
  }
}

.sm-title {
  margin-bottom: 15px;
  font-family: $font-family;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.18;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: $white;
}
.sm-list {
  display: flex;
  column-gap: 15px;
  .sm-item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    border-radius: 6px;
    border: 1px solid rgba(246, 245, 239, 0.5);
    .sm-link {
      .sm-icon {
        fill: $white;
        &.sm-icon-fb {
          width: 8px;
          height: 15px;
          @media screen and (min-width: 768px) {
            width: 10px;
            height: 19px;
          }
        }
        &.sm-icon-insta {
          width: 14px;
          height: 14px;
          @media screen and (min-width: 768px) {
            width: 18px;
            height: 18px;
          }
        }
        &.sm-icon-yt {
          width: 14px;
          height: 14px;
          @media screen and (min-width: 768px) {
            width: 20px;
            height: 20px;
          }
        }
      }
    }

    @media screen and (min-width: 768px) {
      width: 40px;
      height: 40px;
    }
  }
}
.copyright {
  display: block;
  width: 235px;
  margin: 0 auto;
  font-family: $font-family;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.18;
  letter-spacing: 0.03em;
  text-align: center;
  color: rgba(246, 245, 239, 0.5);
}
